import React, { useState, useEffect } from "react";
import {
  Row,
  Col,
  Form,
  Button,
  Select,
  Switch,
  Alert,
  DatePicker,
} from "antd";
import FloatLabel from "./FloatLabels";
import momentUtil from "../../utils/momentUtil";
import UploadImage from "../../components/managePolls/UploadImage";
import PieChart from "../../assets/images/pie-chart.svg";
import BarChart from "../../assets/images/bar-chart.svg";
import MultiSelect from "../managePolls/MultiSelect";
import { TimePicker } from "@progress/kendo-react-dateinputs";
import "../modal/modal.scss";
import "date-fns";
import {
  SHOW_RESULTS_POLL,
  SELECT_START_DATE,
  SELECT_END_DATE,
  TIME_ZONE,
  CLEAR_ALL,
} from "../../labels";
import {
  UPLOAD,
  BACK,
  NEXT,
  CONFIGURE_CUG,
  SCHEDULE,
  SAVE_AS_DRAFT_BTN,
} from "../../headings";
import {
  REQUIRED_MESSAGE,
  UPLOAD_FORMAT_ERROR,
  PAST_TIME,
  SELECT_DATE,
} from "../../errors";
import { PIE_CHART, BAR_CHART } from "../../values";
import moment from "moment";
import { Link } from "react-router-dom";
const DuplicatePollStepTwo = ({
  next,
  cug,
  prev,
  uploadError,
  poll,
  handleStepOneUpload,
  handleBeforeUpload,
  handleOnRemove,
  handlePollImagePreview,
  handleCancel,
  onChange,
  setPoll,
  onSwitchChange,
  setPollsDate,
  handleOnSelect,
  handleOnCUGSelect,
  itemCleared,
  createDuplicatePoll,
}) => {
  const [error, showError] = useState(false);
  const [attachmentError, showAttachmentError] = useState(false);
  const [endTime, setEndTime] = useState(poll.pollStartTime ? poll.pollStartTime : new Date());
  const [startTime, setStartTime] = useState(poll.pollEndTime ? poll.pollEndTime : new Date());
 // const [endTime, setEndTime] = useState(new Date());
  //const [end_Time, setEnd_Time] = useState(new Date());
  //const [startTime, setStartTime] = useState(new Date());
  //const [start_Time, setStart_Time] = useState(new Date());
  const [itemRemoved, setItemRemoved] = useState("");
  const [selectedTimeZone, setSelectedTimeZone] = useState(poll.timezone1 ? poll.timezone1 : "Asia/Kolkata")


  const removeValues = (value) => {
    setItemRemoved(value);
    itemCleared(value);
  };

  // useEffect(() => {
  //   setEndTime(poll.pollEndTime);
  //   setEndTime(poll.pollEndTime);
  //   setStartTime(poll.pollStartTime);
  //   setStartTime(poll.pollStartTime);
  // }, []);

  const onChangeStartTime = (event) => {
    const startTime = event.target.value;
    const formattedStartDate = momentUtil.formatAsDDMMYYYY(poll.pollStartDate);
    const edate = formattedStartDate.split("-")[0];
    const emonth = formattedStartDate.split("-")[1];
    const eyear = formattedStartDate.split("-")[2];
    let minutes = startTime.getMinutes();
    let hours = startTime.getHours();
    if (minutes.toString().length === 1) {
      minutes = "0" + minutes;
    }
    if (hours.toString().length === 1) {
      hours = "0" + hours;
    }
    if (hours.toString() === "0") {
      hours = "00";
    }
    const extractedTime = hours + ":" + minutes + ":" + "00";
    const final_startTime = new Date(
      eyear + "-" + emonth + "-" + edate + "T" + extractedTime
    );
    setStartTime(final_startTime);
    setPoll({ ...poll, pastStartTimeError: false });
  };

  // const onBlurStartTime = (event) => {
  //   const currentTime = new Date();
  //   if (
  //     currentTime > startTime &&
  //     startTime.getTime() !== start_Time.getTime()
  //   ) {
  //     setStartTime(start_Time);
  //     setPoll({ ...poll, pastStartTimeError: true });
  //   } else {
  //     setPoll({ ...poll, pastStartTimeError: false, pollStartTime: startTime });
  //   }
  // };

  const onBlurStartTime = (event) => {

    const currentDateTime = moment().tz(selectedTimeZone).format("YYYY-MM-DDTHH:mm:ss");
    const startTimeDateTime = moment(startTime).format();
    const currentTimeAsPerTimeZone =  currentDateTime;
    const covertStartTimeIntoUnix = startTime;

    console.log("-----covertStartTimeIntoUnix--", covertStartTimeIntoUnix);

    const currentTimeAsPerTimeZoneInDateFormat = new Date(currentTimeAsPerTimeZone);
    const covertStartTimeIntoUnixInDateFormat= startTime;//new Date(covertStartTimeIntoUnix);

    if(currentTimeAsPerTimeZoneInDateFormat < covertStartTimeIntoUnixInDateFormat){
      console.log("selected time zone is greater that current time zone",   "current dateTIme-", currentTimeAsPerTimeZone, " < ", "selected dateTIme-", covertStartTimeIntoUnix);
      setPoll({
        ...poll,
        pastStartTimeError: false,
        pollStartTime: covertStartTimeIntoUnix,
      });
    }else{
      console.log("selected time zone is less that current time zone",  "current dateTIme-", currentTimeAsPerTimeZone, " < ", "selected dateTIme-", covertStartTimeIntoUnix);
      setPoll({
        ...poll,
        pastStartTimeError: true,
        pollStartTime: covertStartTimeIntoUnix,
      });
    }
  };

  const onChangeEndTime = (event) => {
    const endTime = event.target.value;
    const formattedEndDate = momentUtil.formatAsDDMMYYYY(poll.pollEndDate);
    const edate = formattedEndDate.split("-")[0];
    const emonth = formattedEndDate.split("-")[1];
    const eyear = formattedEndDate.split("-")[2];
    let minutes = endTime.getMinutes();
    let hours = endTime.getHours();
    if (minutes.toString().length === 1) {
      minutes = "0" + minutes;
    }
    if (hours.toString().length === 1) {
      hours = "0" + hours;
    }
    if (hours.toString() === "0") {
      hours = "00";
    }
    const extractedTime = hours + ":" + minutes + ":" + "00";
    const final_endTime = new Date(
      eyear + "-" + emonth + "-" + edate + "T" + extractedTime
    );
    setEndTime(final_endTime);
    setPoll({ ...poll, pastEndTimeError: false });
  };


  const onBlurEndTime = (event) => {
    const currentDateTime = moment().tz(selectedTimeZone).format("YYYY-MM-DDTHH:mm:ss");
    const endTimeDateTime = endTime;//moment(endTime).format();
    const currentTimeAsPerTimeZone = currentDateTime; //moment.tz(currentDateTime, poll.timezone1).format("YYYY-MM-DDTHH:mm:ss");
    const covertEndTimeIntoUnix = endTime;//moment.tz(endTimeDateTime, poll.timezone1).format("YYYY-MM-DDTHH:mm:ss");
    const currentTimeAsPerTimeZoneInDateFormat = new Date(currentTimeAsPerTimeZone);
   
    if(currentTimeAsPerTimeZoneInDateFormat < covertEndTimeIntoUnix){
      console.log("selected time zone is greater that current time zone");
      setPoll({
        ...poll,
        pastEndTimeError: false,
        pollEndTime: covertEndTimeIntoUnix,
      });
    }else{
      console.log("selected time zone is less that current time zone");
      setPoll({
        ...poll,
        pastEndTimeError: true,
        pollEndTime: covertEndTimeIntoUnix,
      });
    }
  };


  // const onBlurEndTime = (event) => {
  //   const currentTime = new Date();
  //   if (currentTime > endTime && endTime.getTime() !== end_Time.getTime()) {
  //     setEndTime(end_Time);
  //     setPoll({ ...poll, pastEndTimeError: true });
  //   } else if (endTime < startTime) {
  //     setEndTime(end_Time);
  //     setPoll({ ...poll, pastEndTimeError: true });
  //   } else {
  //     setEndTime(end_Time);
  //     setPoll({ ...poll, pastEndTimeError: false, pollEndTime: endTime });
  //   }
  // };

  const disabledStartDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const disabledEndDate = (current) => {
    if (poll.pollStartDate) {
      return current && current < moment(poll.pollStartDate, "DD-MM-YYYY");
    } else {
      return current && current < moment().startOf("day");
    }
  };

  const onPreview = (e, type) => {
    e.preventDefault();
    if (uploadError) {
      showAttachmentError(true);
    } else if (
      !poll.pollStartDate ||
      (poll.timezone1 && poll.timezone1.length === 0)
    ) {
      if (type === "next") {
        showError(true);
      } else {
        showError(false);
        createDuplicatePoll();
      }
    } else {
      showError(false);
      showAttachmentError(false);
      if (type === "next") {
        next();
      } else {
        createDuplicatePoll();
      }
    }
  };


  const onTimeZoneChangeDateTimeForStartDateTime = (value) => {
    const currentDateTime = moment().tz(value).format("YYYY-MM-DDTHH:mm:ss");
    const currentTimeAsPerTimeZone = currentDateTime;//moment.tz(currentDateTime, value).format("YYYY-MM-DDTHH:mm:ss");
    const dataNewDateFormat = new Date(currentDateTime);
    setStartTime(dataNewDateFormat);
    const dataPoll = poll;
    dataPoll.pastStartTimeError=false;
    dataPoll.pollStartTime = dataNewDateFormat;
    setPoll(dataPoll);
  }

  const onTimeZoneChangeDateTimeForEndDateTime = (value) => {
    const currentDateTime = moment().tz(value).format("YYYY-MM-DDTHH:mm:ss");

    const currentTimeAsPerTimeZone = currentDateTime;//moment.tz(currentDateTime, value).format("YYYY-MM-DDTHH:mm:ss");
    const dataNewDateFormat = new Date(currentTimeAsPerTimeZone);
    setEndTime(dataNewDateFormat);

    const dataPoll = poll;
    dataPoll.pastEndTimeError=false;
    dataPoll.pollEndTime = dataNewDateFormat;
    setPoll(dataPoll);
  }

  const getFunctionCalled = (value) =>{
    setSelectedTimeZone(value);
    handleOnSelect("timezone1", value); 
    onTimeZoneChangeDateTimeForStartDateTime(value);
    onTimeZoneChangeDateTimeForEndDateTime(value);
}

  return (
    <>
      <Col span={24}>
        <Form className="input-md floating-label w-100" name="validate_other">
          <Col span={24}>
            {cug && cug.cug_field && Object.keys(cug?.cug_field).length > 0 ? (
              <>
                <h6 className="mb-4">{CONFIGURE_CUG}</h6>
                <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                  {Object.keys(cug.cug_field).map((item, key) => {
                    if (item.length > 0) {
                      return (
                        <>
                          {/* <Col span={24} lg={8} className="gutter-row mb-3">
                          <FloatLabel
                            label={item.substring(
                              item.indexOf("_") + 1,
                              item.length
                            )}
                          ></FloatLabel>
                        </Col> */}
                          <Col span={24} lg={8} className="gutter-row mb-3">
                            <Form.Item className="mb-0" name={item}>
                              <MultiSelect
                                cugData={cug?.cug_field}
                                pollCug={poll.cugDetails}
                                item={item}
                                handleOnCUGSelect={handleOnCUGSelect}
                                itemRemoved={itemRemoved}
                              ></MultiSelect>
                            </Form.Item>
                          </Col>
                          {/* <Col
                          span={4}
                          lg={2}
                          className="gutter-row d-flex align-items-center mt-2 mt-lg-0"
                        >
                          <Button onClick={() => removeValues(item)}>
                            {CLEAR_ALL}
                          </Button>
                        </Col> */}
                        </>
                      );
                    }
                  })}
                </Row>
              </>
            ) : null}
            <Row>
              <h6>{UPLOAD}</h6>
              <Col span={24} className="gutter-row">
                {attachmentError && (
                  <Form.Item>
                    <Alert
                      message={UPLOAD_FORMAT_ERROR}
                      type="error"
                      showIcon
                    ></Alert>
                  </Form.Item>
                )}
                <Form.Item name="upload" className="upload-list">
                  <UploadImage
                    file={poll.pollDocument}
                    handleStepOneUpload={handleStepOneUpload}
                    handleBeforeUpload={handleBeforeUpload}
                    handleOnRemove={handleOnRemove}
                    handlePollImagePreview={handlePollImagePreview}
                    handleCancel={handleCancel}
                    poll={poll}
                  />
                </Form.Item>
              </Col>
            </Row>
            <Col span={24} className="gutter-row mt-5">
              <Row>
                <Col span={24} md={8} lg={8} xl={6} className="mb-3 t-2 mb-y-0">
                  <Form.Item label={SHOW_RESULTS_POLL} className="mb-0">
                    <Switch
                      id="showResult"
                      checked={poll.showResult}
                      onChange={(checked, e) => onChange(checked, e)}
                    />
                  </Form.Item>
                </Col>
                {poll.showResult ? (
                  <Col span={24}>
                    <Col span={24} lg={12}>
                      {poll.switch ? (
                        <img className="w-100" src={PieChart} alt={PIE_CHART} />
                      ) : (
                        <img className="w-100" src={BarChart} alt={BAR_CHART} />
                      )}
                    </Col>
                    <Col className="toggle-switch mt-3 pl-5">
                      <Switch
                        id="showChart"
                        checkedChildren={BAR_CHART}
                        unCheckedChildren={PIE_CHART}
                        onChange={(checked, e) => onSwitchChange(checked, e)}
                      />
                    </Col>
                  </Col>
                ) : (
                  <Col></Col>
                )}
              </Row>
            </Col>
            <Col span={24} className="gutter-row mt-4 mb-3">
              <h6>{SCHEDULE}</h6>
              {error && (
                <Form.Item>
                  <Alert
                    message={REQUIRED_MESSAGE}
                    type="error"
                    showIcon
                  ></Alert>
                </Form.Item>
              )}
              <Col span="24" lg={8} className="mb-3 mb-lg-0">
                <FloatLabel label="Time Zone" value={poll.timezone1}>
                  <Form.Item className="required mb-0" name="timezone1">
                    <Select
                      className=""
                      htmlFor="timezone1"
                      onSelect={(value) => getFunctionCalled(value)}
                      defaultValue={poll.timezone1}
                    >
                      {moment.tz.names().map((value) =>{ 
                           const dateForUTC = moment().format("YYYY-MM-DDTHH:mm:ss");
                           const momnetoffset = moment.utc(dateForUTC).tz(`${value}`);
                           const timeZoneOffset = momnetoffset ? momnetoffset.format("Z"):"";
                           const valueWithUTC = `(UTC ${timeZoneOffset}) `+value;
                        return (
                        <Select.Option value={value}>{valueWithUTC}</Select.Option>
                      )
                      })}
                    </Select>
                  </Form.Item>
                </FloatLabel>
              </Col>
              <FloatLabel label={SELECT_START_DATE} value="sDate">
                <Form.Item
                  name="sDate"
                  rules={[
                    {
                      required: true,
                    },
                  ]}
                >
                  <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                    <Col span="24" lg={8} className="gutter-row mb-3 mb-lg-0">
                      <DatePicker
                        className="w-100"
                        name="sDate"
                        className="required"
                        value={poll.pollStartDate}
                        disabledDate={disabledStartDate}
                        onChange={(date) => setPollsDate("pollStartDate", date)}
                      />
                      {poll.pastStartTimeError && (
                        <Alert
                          message={PAST_TIME}
                          type="error"
                          showIcon
                        ></Alert>
                      )}
                    </Col>
                    <Col span="24" lg={8} className="mb-3 mb-lg-0">
                      <TimePicker
                        value={startTime}
                        onChange={onChangeStartTime}
                        onBlur={onBlurStartTime}
                      />
                    </Col>
                    {/* <Col span="24" lg={8} className="mb-3 mb-lg-0">
                      <FloatLabel label="Time Zone" value={poll.timezone1}>
                        <Form.Item className="required mb-0" name="timezone1">
                          <Select
                            className=""
                            htmlFor="timezone1"
                            onSelect={(value) =>
                              handleOnSelect("timezone1", value)
                            }
                            defaultValue={poll.timezone1}
                          >
                            {moment.tz.names().map((value) => (
                              <Select.Option value={value}>
                                {value}
                              </Select.Option>
                            ))}
                          </Select>
                        </Form.Item>
                      </FloatLabel>
                    </Col> */}
                  </Row>
                </Form.Item>
              </FloatLabel>
              <FloatLabel label={SELECT_END_DATE} value="eDate">
                <Form.Item name="eDate">
                  <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                    <Col span="24" lg={8} className="gutter-row mb-3 mb-lg-0">
                      <DatePicker
                        className="w-100"
                        name="eDate"
                        value={poll.pollEndDate}
                        disabledDate={disabledEndDate}
                        disabled={
                          poll.pollStartDate?.length === 0 ||
                          !poll.pollStartDate
                        }
                        onChange={(date) => setPollsDate("pollEndDate", date)}
                      />
                      {poll.pastEndTimeError && (
                        <Alert
                          message={PAST_TIME}
                          type="error"
                          showIcon
                        ></Alert>
                      )}
                    </Col>
                    <Col span="24" lg={8} className="mb-3 mb-lg-0">
                      <TimePicker
                        value={endTime}
                        onChange={onChangeEndTime}
                        onBlur={onBlurEndTime}
                      />
                    </Col>
                    {/* <Col span="24" lg={8} className="mb-3 mb-lg-0">
                      <FloatLabel label={TIME_ZONE} value={poll.timezone2}>
                        <Form.Item className="mb-0" name="timezone2">
                          <Select
                            className=""
                            htmlFor="timezone2"
                            onSelect={(value) =>
                              handleOnSelect("timezone2", value)
                            }
                            defaultValue={poll.timezone2}
                          >
                            {moment.tz.names().map((value) => (
                              <Select.Option value={value}>
                                {value}
                              </Select.Option>
                            ))}
                          </Select>
                        </Form.Item>
                      </FloatLabel>
                    </Col> */}
                  </Row>
                </Form.Item>
              </FloatLabel>
            </Col>
            <Button
              className="mr-4"
              type="secondary"
              onClick={prev}
              data-test="steptwoPrev"
            >
              {BACK}
            </Button>
            <Button
              className="mr-4"
              type="primary"
              onClick={(e) => onPreview(e, "next")}
              data-test="steptwoNext"
            >
              {NEXT}
            </Button>
            <Button
              type="link"
              className="link"
              onClick={(e) => onPreview(e, "draft")}
            >
              <Link>
                <b className="pl-1"> {SAVE_AS_DRAFT_BTN}</b>
              </Link>
            </Button>
            {/* <Button
              type="primary"
              danger
              onClick={(e) => onPreview(e, "draft")}
            >
              {SAVE_AS_DRAFT_BTN}
            </Button> */}
          </Col>
        </Form>
      </Col>
    </>
  );
};

export default DuplicatePollStepTwo;
