import React, { useEffect, useState } from "react";
import ShowAllEmployees from "./ShowAllEmployees";
import { connect } from "react-redux";
import { getManageEmpAsync } from "../../actions/manageEmp";
import getCUG from "../../actions/cug";
import { Form } from "antd";
const EmployeesContainer = (props) => {
  const [statusClicked, setStatusClicked] = useState(false);
  const [loader, setLoader] = useState(false);
  const [filter, setFilter] = useState({ 
    search: "",
    status_id: [],
    cug_filter: {}});
  const [search, setSearch] = useState("");
  const [employees, setEmployees] = useState([]);
  const [form] = Form.useForm();
  const [checked, setChecked] = useState(false);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getManageEmpAsync(filter, search));
    setLoader(true);
  }, [statusClicked,filter]);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getCUG());
  }, []);
  
  useEffect(() => {
    if (props.manageEmpData === null) {
      setLoader(true);
    } else if (props.manageEmpData !== null) {
      setLoader(false);
    }
    const polls = props.manageEmpData !== null ? props.manageEmpData : [];
     const isInactiveEmpPresent = filter?.status_id?.includes(4);
    // if (
    //   filter?.search?.length > 0 ||
    //   (Object.values(filter?.status_id).length > 0 && !isInactiveEmpPresent)
    // ) {
    //   setEmployees(polls);
    // } 
    setEmployees(props.manageEmpData);
  }, [props.manageEmpData]);

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  const setSearchValue = (value) => {
    setSearch(value);
  };
 
  const setFilterOptions = (type, value) => {
    if (type === "reset") {
      form.setFieldsValue({ search: "" });
    }
    setFilter(value);
  };
  // const setFilterOptions = (value) => {
  //   setFilter(value);
  // };
  const handleCheck = (data) => {
    setChecked(!checked);
    setFilter(data);
  };

  return (
    <ShowAllEmployees
      statusOnClicked={statusOnClicked}
      employees={employees}
      data={props}
      form={form}
      checked={checked}
      cug={props.cug}
      loader={loader}
      setLoader={setLoader}
      setFilterOptions={setFilterOptions}
      setSearchValue={setSearchValue}
      filter={filter}
      handleCheck={handleCheck}
    />
  );
};

function mapStateToProps(state) {
  const { manageEmpData } = state.manageEmp;
  const { message } = state.message;
  const { cug } = state.cug;
  return {
    manageEmpData,
    message,
    cug,
  };
}

export default connect(mapStateToProps)(EmployeesContainer);
