import React, { useEffect, useState } from "react";
import ManageBroadcast from "./ManageBroadcast";
import BroadcastMessages from "./BroadcastMessages";
import { connect } from "react-redux";
import { getBroadCastMessages } from "../../actions/broadCast";
import getCUG from "../../actions/cug";

const BroadcastContainer = (props) => {
  const [loader, setLoader] = useState(false);
  const [filter, setFilter] = useState([]);
  const [search, setSearch] = useState("");
  const [sort, setSort] = useState("DESC");
  const [broadCastdata, setbroadCastdata] = useState([]);


  useEffect(() => {
    const { dispatch } = props;
    dispatch(getCUG());
  }, []);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getBroadCastMessages(filter, search, sort));
  }, [filter, search, sort]);

  useEffect(() => {
    if (props.broadcastmessagedata === null) {
      setLoader(true);
    } else if (props.broadcastmessagedata !== null) {

      console.log("-------check useEffect called ");

      const broadCastDataBind = props.broadcastmessagedata && props.broadcastmessagedata.map((item) => {
        item.showText = false;
        return item;
      });
      setbroadCastdata(broadCastDataBind);
      setLoader(false);
    }
  }, [props.broadcastmessagedata]);

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  const setSearchValue = (value) => {
    setSearch(value);
  };

  const setFilterOptions = (value) => {
    setFilter(value);
    setLoader(true);
  };

  const setSortOptions = (value) => {
    setSort(value);
    setLoader(true);
  };

  const updateBroadCastArray = (type, boradCastId) => {
    console.log("-------check data ", type, boradCastId);
    const broadCastDataBind = props.broadcastmessagedata && props.broadcastmessagedata.map((item) => {
      if(boradCastId == item.broadcast_id){
        item.showText = type;
      }
      return item;
    });
    setbroadCastdata(broadCastDataBind);
  }
 

  return (
    <BroadcastMessages
      broadcastmessages={broadCastdata}
      statusOnClicked={statusOnClicked}
      setFilterOptions={setFilterOptions}
      setSearchValue={setSearchValue}
      setSortOptions={setSortOptions}
      updateBroadCastArray={updateBroadCastArray}
      data={props}
      loader={loader}
      cug={props.cug?.cug_field}
    />
  );
};

function mapStateToProps(state) {
  const { broadcastmessagedata } = state.broadcastMessages;
  const { cug } = state.cug;

  return {
    broadcastmessagedata,
    cug
  };
}

export default connect(mapStateToProps)(BroadcastContainer);
