import React from "react";
import { TreeSelect } from "antd";


import { AM_WEB, ORG_CC, APP_USER, IDB_USER, SUPER_USER } from "../../values";
import { APPLY_FILTER } from "../../headings";

const { SHOW_PARENT } = TreeSelect;

class MultiSelection extends React.Component {
  state = {
    value: undefined,
    treeData: [],
  };

  componentDidMount() {
    const statusTreeData = [
      {
        title: AM_WEB,
        value: "1",
        key: "1",
      },
      {
        title: ORG_CC,
        value: "2",
        key: "2",
      },
      {
        title: APP_USER,
        value: "3",
        key: "3",
      },
      {
        title: IDB_USER,
        value: "4",
        key: "4",
      },
      {
        title: SUPER_USER,
        value: "5",
        key: "5",
      },
        ];
    const cugTreeData = [];
    if (this.props.status) {
      this.setState({ treeData: statusTreeData });
    } else {
      this.props.cugData[this.props.item]?.map((value) => {
        cugTreeData.push({
          title: value,
          value: value,
          key: value,
        });
      });
      this.setState({
        treeData: cugTreeData,
      });
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.reset !== prevProps.reset && this.props.reset) {
      this.setState({ value: undefined });
    }
  }

  onChange = (value) => {
    this.setState({ value });
    let type = "";
    if (this.props.cug) {
      type = "cug_filter";
    } else {
      type = "status_id";
    }
    this.props.handlePollOnSelect(value, type, this.props.item);
  };

  render() {
    const tProps = {
      treeData: this.state.treeData,
      value: this.state.value,
      onChange: this.onChange,
      treeCheckable: true,
      placeholder: APPLY_FILTER,
      showCheckedStrategy: SHOW_PARENT,
      style: {
        width: "100%",
      },
    };
    return <TreeSelect {...tProps} />;
  }
}

export default MultiSelection;
