import React, { useState, useEffect } from "react";
import {
  Layout,
  Row,
  Col,
  Breadcrumb,
  Button,
  Form,
  Input,
  DatePicker,
  Alert,
  Spin,
} from "antd";
import { NavLink, useHistory } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import "react-phone-input-2/lib/style.css";
import OrgSidebar from "../../containers/OrgSidebar";
import "../form/form.scss";
import FloatLabel from "../form/FloatLabels";
import PhoneInput from "react-phone-input-2";
import moment from "moment";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import * as AppStorage from "../../utils/AppStorage";
import authHeader from "../../services/auth-header";
import { ADD_EMPLOYEE, SAVE, CANCEL, SAVE_AS_DRAFT_BTN } from "../../headings";
import { ADD_EMPLOYEE_TEXT_PART4 } from "../../messages";
import {
  REQUIRED_MESSAGE,
  SELECT_DATE,
  VALID_EMAIL,
  VALID_MOBILE,
  EMAIL_ALREADY_EXISTS,
  MOB_ALREADY_EXISTS
} from "../../errors";
import {
  EMAIL,
  FIRST_NAME,
  LAST_NAME,
  MOBILE,
  START_DATE,
  END_DATE,
} from "../../labels";
import MultiRoleSelection from "./MultiRoleSelection";
import { DownOutlined, CheckCircleTwoTone } from "@ant-design/icons";
import { Link } from "react-router-dom";
var CryptoJS = require("crypto-js");
const { Content } = Layout;
const CreateManually = (props) => {
  const [userDetails, setUserDetails] = useState({
    firstName: "",
    lastName: "",
    status: "",
    email: "",
    //date: "",
    mobile: "",
    empId: "",
    department: "",
    role: ["0"],
    startDate: "",
    endDate: "",
  });

  const [userRoles, setUserRoles] = useState({
    0: "1",
    1: "0",
    2: "0",
    3: "0",
})

  const commonForAllUsers = JSON.parse(AppStorage.get("user"));
  const app_Id = CryptoJS.AES.decrypt(
    commonForAllUsers.user_data.app_authentication_key_id,
    "anonymoose"
  ).toString(CryptoJS.enc.Utf8);
  const history = useHistory();
  const [error, showError] = useState(false);
  const [loader, setLoader] = useState(false);
  const [emailValidationError, setEmailValidationError] = useState(false);
  const [mobileValidationError, setMobileValidationError] = useState(false);
  const [isToday, checkToday] = useState(false);
  const [isChecked, setChecked] = useState(false);
  const [CUGFields, setCUGFields] = useState({});
  const [flexFields, setFlexFields] = useState({});
  const [empErrorMessage, setEmpErrorMessage] = useState("");
  const [empError, setEmpError] = useState(false);
  const [employeeErrorUpdated, setEmployeeErrorUpdated] = useState("");
  const [employeeMobError, setEmployeeMobError] = useState("");


  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios.get(BASE_URL + `getCUGFieldNames/`, options).then((res) => {
      let cug_Fields = {};
      let flex_Fields = {};
      if (Object.keys(res.data.cug_flex_fields).length !== 0) {
        const cug_data = Object.values(
          res.data.cug_flex_fields?.cug_fields
        ).reduce((accumulator, currentValue) => {
          cug_Fields = { ...cug_Fields, [currentValue]: null };
          return cug_Fields;
        }, []);
        setCUGFields(cug_data);
        const flex_data = Object.values(
          res.data.cug_flex_fields?.flex_fields
        ).reduce((accumulator, currentValue) => {
          flex_Fields = { ...flex_Fields, [currentValue]: null };
          return flex_Fields;
        }, []);
        setFlexFields(flex_data);
      }
    });
  }, []);

  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios.get(BASE_URL + `getCUGFieldNames/`, options).then((res) => {
      let cug_Fields = {};
      let flex_Fields = {};
      if (Object.keys(res.data.cug_flex_fields).length !== 0) {
        const cug_data = Object.values(
          res.data.cug_flex_fields?.cug_fields
        ).reduce((accumulator, currentValue) => {
          cug_Fields = { ...cug_Fields, [currentValue]: null };
          return cug_Fields;
        }, []);
        setCUGFields(cug_data);
        const flex_data = Object.values(
          res.data.cug_flex_fields?.flex_fields
        ).reduce((accumulator, currentValue) => {
          flex_Fields = { ...flex_Fields, [currentValue]: null };
          return flex_Fields;
        }, []);
        setFlexFields(flex_data);
      }
    });
  }, []);

  const handleUserOnChange = (e) => {
    setUserDetails({
      ...userDetails,
      [e.target.id]: e.target.value,
    });
  };

  const handleCUGFieldOnChange = (e, type) => {
    if (type === "cug") {
      if(e.target.value == ""){
        setCUGFields({
          ...CUGFields,
          [e.target.id]: null,
        });
      }else{
        setCUGFields({
          ...CUGFields,
          [e.target.id]: e.target.value,
        });
      }
    }
    if (type === "flex") {
      if(e.target.value == ""){
        setFlexFields({
          ...flexFields,
          [e.target.id]: null,
        });
      }else{
        setFlexFields({
          ...flexFields,
          [e.target.id]: e.target.value,
        });
      }
     
    }
  };

  const handleMobile = (mobile) => {
    setUserDetails({
      ...userDetails,
      mobile: mobile,
    });
  };

  const handleUserOnSelect = (value, name) => {
    setUserDetails({ ...userDetails, [name]: value });
  };

  const handleUserDatePickerChange = (date, name) => {
    if (moment().isSame(date, "day")) {
      checkToday(true);
    } else {
      checkToday(false);
    }
    setUserDetails({ ...userDetails, [name]: date });
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const validateEmailAddress = (e) => {
    var re = /\S+@\S+\.\S+/;
    if (!re.test(e.target.value)) {
      setEmailValidationError(true);
    } else {
      setEmailValidationError(false);
    }
    checkEmailExist();
  };

  const checkEmailExist = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .post(
        BASE_URL + "checkEmail/",
        { email: userDetails.email },

        options
      )
      .then((res) => {
        if (res.data) {
          setEmployeeErrorUpdated(res.data.message);
        }
      });
  };

  const validateMobile = (e) => {
    var regex = /^\+\d$/;
    if (userDetails.mobile.length > 10 || regex.test(e.target.value)) {
      setMobileValidationError(false);
    } else {
      setMobileValidationError(true);
    }
    checkMobileNoExist();
  };

  const checkMobileNoExist = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .post(
        BASE_URL + "checkPhone/",
        { mobile_number: userDetails.mobile },
        options
      )
      .then((res) => {
        if (res.data) {
          setEmployeeMobError(res.data.message);
        }
      });
  };

  const onSubmit = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    if (
      userDetails.firstName &&
      userDetails.lastName &&
      userDetails.email &&
      // userDetails.status &&
      //userDetails.role &&
      userDetails.startDate &&
      userDetails.mobile.length > 5
    ) {
      let data = {};
      if (app_Id === "1" || app_Id === "3") {
        showError(false);
        data = {
          first_name: userDetails.firstName,
          last_name: userDetails.lastName,
          email: userDetails.email,
          mobile_number: userDetails.mobile ? "+" + userDetails.mobile : "",
          is_super_user : userRoles[1] == "1" ? true : false,
          is_org_cc_user : userRoles[2] == "1" ? true : false,
          is_idb_user : userRoles[3] == "1" ? true : false,
          start_date: userDetails.startDate
            ? moment(userDetails.startDate).format("DD-MM-YYYY")
            : null,
          end_date: userDetails.endDate
            ? moment(userDetails.endDate).format("DD-MM-YYYY")
            : null,
          status_id: 1,
          cug_fields: CUGFields,
          flex_fields: flexFields,
        };
        setLoader(true);

        axios
          .post(BASE_URL + "createUser/", data, options)
          .then(() => {
            window.location = "/orgcc/manageemployees";
          })
          .catch((error) => {
            if (error.message.includes("400")) {
              setEmpError(true);
              setEmpErrorMessage(error.response.data.email);
            }
            setLoader(false);
          });
      } else {
        if (userDetails.mobile) {
          showError(false);
          data = {
            first_name: userDetails.firstName,
            last_name: userDetails.lastName,
            email: userDetails.email,
            mobile_number: userDetails.mobile ? "+" + userDetails.mobile : "",
            is_super_user : userRoles[1] == "1" ? true : false,
            is_org_cc_user : userRoles[2] == "1" ? true : false,
            is_idb_user : userRoles[3] == "1" ? true : false,
            start_date: userDetails.startDate
              ? moment(userDetails.startDate).format("DD-MM-YYYY")
              : null,
            end_date: userDetails.endDate
              ? moment(userDetails.endDate).format("DD-MM-YYYY")
              : null,
            status_id: 1,
            cug_fields: CUGFields,
            flex_fields: flexFields,
          };
          setLoader(true);
          axios
            .post(BASE_URL + "createUser/", data, options)
            .then(() => {
              window.location = "/orgcc/manageemployees";
            })
            .catch((error) => {
              if (error.message.includes("400")) {
                setEmpError(true);
                setEmpErrorMessage(error.response.data.email);
              }
              setLoader(false);
            });
        } else {
          showError(true);
        }
      }
    } else {
      showError(true);
    }
  };

  const onSaveDraft = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    if (userDetails.email) {
      const data = {
        first_name: userDetails.firstName,
        last_name: userDetails.lastName,
        email: userDetails.email,
        mobile_number: userDetails.mobile ? "+" + userDetails.mobile : "",
        is_super_user : userRoles[1] == "1" ? true : false,
        is_org_cc_user : userRoles[2] == "1" ? true : false,
        is_idb_user : userRoles[3] == "1" ? true : false,
        start_date: userDetails.startDate
          ? moment(userDetails.startDate).format("DD-MM-YYYY")
          : null,
        end_date: userDetails.endDate
          ? moment(userDetails.endDate).format("DD-MM-YYYY")
          : null,
        status_id: 2,
        cug_fields: CUGFields,
        flex_fields: flexFields,
      };

      showError(false);
      setLoader(true);
      axios
        .post(BASE_URL + "createUser/", data, options)
        .then(() => {
          setLoader(false);
          window.location = "/orgcc/manageemployees";
        })
        .catch((error) => {
          if (error.message.includes("400")) {
            setEmpError(true);
            setEmpErrorMessage(error.response.data.email);
          }
          setLoader(false);
        });
    } else {
      showError(true);
    }
  };

  const onCancel = () => {
    history.push("/orgcc/addemployee");
  };



  const handleRoleOnSelect = (value, label, extra) => {

    console.log("----ttttttttttt----", value, label, extra);
    const valueInstring = value.toString();
    if(extra.triggerValue == 1){
        const roles = {
          0: "1",
          1: valueInstring.includes("1") ? "1" : "0",
          2: valueInstring.includes("1") ? "1" : "0",
          3: valueInstring.includes("1") ? "1" : "0",
      };
      setUserRoles(roles);
        //setUserDetails({ ...userDetails, role: roles });
    }else  if(extra.triggerValue == 2){
        const roles = {
            0: "1",
            1: valueInstring.includes("2") && valueInstring.includes("1") ? "1" : "0",
            2: valueInstring.includes("2") ? "1" : "0",
            3: valueInstring.includes("3") ? "1" : "0",
        };
        setUserRoles(roles);
          //setUserDetails({ ...userDetails, role: roles });
      }else  if(extra.triggerValue == 3){
      const roles = {
          0: "1",
          1: valueInstring.includes("3") && valueInstring.includes("1") ? "1" : "0",
          2: valueInstring.includes("2") ? "1" : "0",
          3: valueInstring.includes("3") ? "1" : "0",
      };
      setUserRoles(roles);
      //setUserDetails({ ...userDetails, role: roles });
  }
}


  // const handleRoleOnSelect = (value, label, extra) => {
  //   if (extra.triggerValue === "1" && extra.checked) {
  //     setUserDetails({ ...userDetails, role: ["0", "1", "2", "3"] });
  //   } else if (extra.triggerValue === "2" && !extra.checked) {
  //     setUserDetails({
  //       ...userDetails,
  //       role: value.filter((v) => v !== "1" && v !== "2"),
  //     });
  //   } else if (extra.triggerValue === "3" && !extra.checked) {
  //     setUserDetails({
  //       ...userDetails,
  //       role: value.filter((v) => v !== "1" && v !== "3"),
  //     });
  //   } else {
  //     setUserDetails({ ...userDetails, role: value });
  //   }
  // };
  

  const onChangeCheckbox = (e) => {
    setChecked(e.target.checked);
  };

  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props} />
              <Content className="w-100">
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/orgcc/addemployee">
                      <Breadcrumb.Item>{ADD_EMPLOYEE}</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{ADD_EMPLOYEE_TEXT_PART4}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">{ADD_EMPLOYEE_TEXT_PART4}</h1>
                </Row>
                <div className="mid-content mt-4">
                  <Row className="d-flex justify-content-center align-items-center">
                    <Spin spinning={loader} size="large">
                      <Form className="input-md floating-label w-100" name="">
                        {error && (
                          <Form.Item>
                            <Alert
                              message={REQUIRED_MESSAGE}
                              type="error"
                              showIcon
                            ></Alert>
                          </Form.Item>
                        )}

                        {empError && (
                          <Form.Item>
                            <Alert
                              message={empErrorMessage}
                              type="error"
                              showIcon
                            ></Alert>
                          </Form.Item>
                        )}
                        <Col span={24}>
                          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={FIRST_NAME}
                                value={userDetails.firstName}
                              >
                                <Form.Item
                                  className="required"
                                  name="firstName"
                                >
                                  <Input
                                    className="input-control"
                                    id="firstName"
                                    htmlFor="firstName"
                                    onChange={(e) => handleUserOnChange(e)}
                                    autoComplete="off"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={LAST_NAME}
                                value={userDetails.lastName}
                              >
                                <Form.Item
                                  className="required"
                                  name="lastName"
                                  validateStatus="success"
                                >
                                  <Input
                                    className="input-control"
                                    id="lastName"
                                    htmlFor="lastName"
                                    onChange={(e) => handleUserOnChange(e)}
                                    autoComplete="off"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={EMAIL}
                                value={userDetails.email}
                              >
                                <Form.Item
                                  className="required"
                                  name="email"
                                  validateStatus="success"
                                >
                                  <Input
                                    type="email"
                                    className="input-control"
                                    id="email"
                                    htmlFor="email"
                                    value={userDetails.email}
                                    onChange={(e) => handleUserOnChange(e)}
                                    onBlur={(e) => validateEmailAddress(e)}
                                    autoComplete="off"
                                  />
                                  {emailValidationError && (
                                    <Alert
                                      message={VALID_EMAIL}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                  {!employeeErrorUpdated &&
                                    userDetails.email
                                      .replace(/^\s*/, "")
                                      .replace(/\s*$/, "").length > 0 &&
                                    userDetails.email.length > 0 && (
                                      <CheckCircleTwoTone
                                        className="successCheck"
                                        twoToneColor="#52c41a"
                                      />
                                    )}
                                  {employeeErrorUpdated && (
                                    <Alert
                                      message={EMAIL_ALREADY_EXISTS}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col
                              span={24}
                              md={8}
                              className="gutter-row phone-field"
                            >
                              <FloatLabel
                                label={MOBILE}
                                value={userDetails.mobile}
                              >
                                <Form.Item
                                  className={
                                    app_Id === "1" || app_Id === "3"
                                      ? ""
                                      : "required"
                                  }
                                  name="mobile"
                                  validateStatus="success"
                                >
                                  <PhoneInput
                                    country="us"
                                    id="mobile"
                                    htmlFor="mobile"
                                    value={userDetails.mobile}
                                    onChange={handleMobile}
                                    onBlur={validateMobile}
                                    autoComplete="off"
                                  />
                                  {mobileValidationError && (
                                    <Alert
                                      message={VALID_MOBILE}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                  {!employeeMobError &&
                                    userDetails.mobile.length > 0 && (
                                      <CheckCircleTwoTone
                                        className="successCheck"
                                        twoToneColor="#52c41a"
                                      />
                                    )}
                                  {employeeMobError && (
                                    <Alert
                                      message={MOB_ALREADY_EXISTS}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={4} className="gutter-row">
                              <FloatLabel label={START_DATE} value="date">
                                <Form.Item
                                  className="input-control datepicker-wrapper required"
                                  rules={[
                                    {
                                      required: true,
                                      message: SELECT_DATE,
                                    },
                                  ]}
                                >
                                  <DatePicker
                                    className="w-100 "
                                    name="startDate"
                                    value={userDetails.startDate}
                                    onChange={(date) =>
                                      handleUserDatePickerChange(
                                        date,
                                        "startDate"
                                      )
                                    }
                                    disabledDate={disabledDate}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={4} className="gutter-row">
                              <FloatLabel label={END_DATE} value="date">
                                <Form.Item className="input-control datepicker-wrapper">
                                  <DatePicker
                                    className="w-100 "
                                    name="endDate"
                                    value={userDetails.endDate}
                                    onChange={(date) =>
                                      handleUserDatePickerChange(
                                        date,
                                        "endDate"
                                      )
                                    }
                                    disabledDate={disabledDate}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>

                            <Col span={24} md={8} className="gutter-row">
                              <MultiRoleSelection
                                handleRoleOnSelect={handleRoleOnSelect}
                                roles={userRoles}
                                viaEdit
                              />
                              <DownOutlined className="down-arrow-ico multiselect-arrow" />
                            </Col>
                          </Row>
                        </Col>
                        <Col span={24}>
                          <Row className="py-3">
                            <span className="mb-0">CUG Fields</span>
                          </Row>

                          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                            {Object.keys(CUGFields).map((c) => (
                              <Col span={24} md={8} className="gutter-row">
                                <FloatLabel label={c} value={CUGFields[c]}>
                                  <Form.Item name={c} validateStatus="success">
                                    <Input
                                      className="input-control"
                                      id={c}
                                      value={CUGFields[c]}
                                      htmlFor={c}
                                      onChange={(e) =>
                                        handleCUGFieldOnChange(e, "cug")
                                      }
                                      autoComplete="off"
                                    />
                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                            ))}
                          </Row>
                        </Col>
                        <Col span={24}>
                          <Row className="py-3">
                            <span className="mb-0">Flex fields</span>
                          </Row>
                          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                            {Object.keys(flexFields).map((f) => (
                              <Col span={24} md={8} className="gutter-row">
                                <FloatLabel label={f} value={flexFields[f]}>
                                  <Form.Item name={f} validateStatus="success">
                                    <Input
                                      className="input-control"
                                      id={f}
                                      value={flexFields[f]}
                                      htmlFor={f}
                                      onChange={(e) =>
                                        handleCUGFieldOnChange(e, "flex")
                                      }
                                      autoComplete="off"
                                    />
                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                            ))}
                          </Row>
                        </Col>

                        <Form.Item className="mt-5">
                          <Button
                            type="primary"
                            htmlType="submit"
                            className="mr-4 mb-3"
                            onClick={onSubmit}
                          >
                            {SAVE}
                          </Button>
                          <Button
                            type="secondary"
                            className="mr-4"
                            onClick={onCancel}
                          >
                            {CANCEL}
                          </Button>
                          <Button
                            type="link"
                            className="link"
                            onClick={onSaveDraft}
                          >
                            <Link>
                              <b className="pl-1"> {SAVE_AS_DRAFT_BTN}</b>
                            </Link>
                          </Button>
                          {/* <Button type="primary" danger onClick={onSaveDraft}>
                            {SAVE_AS_DRAFT_BTN}
                          </Button> */}
                        </Form.Item>
                      </Form>
                    </Spin>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default CreateManually;
