import React, { useState, useEffect } from "react";
import {
  Layout,
  Row,
  Col,
  Button,
  Input,
  Select,
  Divider,
  Upload,
  Image,
  Spin,
  Typography,
  Modal,
} from "antd";
import { Link } from "react-router-dom";
import ReactHtmlParser from "react-html-parser";
import ShowMoreText from "react-show-more-text";
import moment from "moment";

const fileobj = [
  {
    uid: "-1",
    name: "image.png",
    status: "done",
    url:
      "https://amsstagingresources.s3-us-west-1.amazonaws.com/broadcast-icons/2/cf1a4b54-0eb0-4724-9dc9-e766996c7a5a.jpeg",
    thumbUrl:
      "https://amstagingresources.s3-us-west-1.amazonaws.com/broadcast-icons/2/cf1a4b54-0eb0-4724-9dc9-e766996c7a5a.jpeg",
  },
];

const { Content } = Layout;
const { Paragraph } = Typography;

const { Search } = Input;

const ShortAnsLongAnsPollsAns = ({ data, showDownloadOptions }) => {
  const [showText, setShowText] = useState(false);
  const [inputSearchValue, setinputSearchValue] = useState("");
  const [showTextArray, sethowTextArray] = useState([]);

  useEffect(() => {
    const dataArray =
      data &&
      data.users &&
      data.users.length > 0 &&
      data.users.map((item) => {
        item.showText = false;
        return item;
      });
    sethowTextArray(dataArray);
  }, [data]);

  const onSearch = (value) => {
    console.log("-----value------", value);
    // const consolidatedObj = {
    //   status_id: selectedStatus,
    //   cug_fliter: cug_fliterData,
    //   search:value
    // }
    // setFilterOptions(consolidatedObj);
    // //setSearchValue(value);
    setinputSearchValue(value);
  };

  const boldSubstring = (str, inputSearchValue) => {
    if (inputSearchValue) {
      return str.replace(
        new RegExp(inputSearchValue, "gi"),
        (match) => `<mark>${match}</mark>`
      );
    } else {
      return str;
    }
  };

  const showDownloadOptionsFomrShor = (value) => {
    showDownloadOptions(value);
  };

  const changeShowMoreLess = (type, firstName, lastName) => {
    console.log("---------showTextArray----------", showTextArray);

    const newArrayData =
      showTextArray &&
      showTextArray.map((item) => {
        if (item.first_name == firstName && item.last_name == lastName) {
          item.showText = type;
        }
        return item;
      });
    sethowTextArray(newArrayData);
  };

  return (
    <React.Fragment>
      {showTextArray && showTextArray && showTextArray.length > 0 && (
        <Row className="w-100">
          <Col span={24}>
            <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
              <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                <Row>
                  <Col span={24} lg={9} className="search-box">
                    <Search placeholder="Search" onSearch={onSearch} />
                  </Col>
                </Row>
              </Col>
            </Row>
          </Col>
         
            <div className="w-100">
              {showTextArray &&
                showTextArray?.map((value, index) => {
                  const messageContent = value.Answer;
                  let plainText = messageContent
                    ? messageContent.replace(/<[^>]+>/g, "")
                    : "No Data";
                  let plainShortText = plainText.slice(0, 500);
                  const textToDisplay =
                    plainShortText.length < 500 ? false : true;
                  const userName = value.first_name + " " + value.last_name;
                  //showTextArray, sethowTextArray
                  const userIndex = index +1;
                  return (
                    <Row className="w-100">
                      <Col span={22}>
                        <h6 className="font-weight-md mb-1">
                          {"User " + userIndex}
                        </h6>
                        <div style={{ paddingBottom: 8 }}>
                          <small>
                            {"created On: "}
                            {moment(value.created_on)
                              .utc()
                              .local()
                              .format("MM/DD/YYYY")}
                          </small>
                        </div>
                        <div style={{ paddingBottom: 8 }}>
                          <div>
                            {/* showTextArray, sethowTextArray */}
                            {value.showText == false && (
                              <p>
                                {" "}
                                {ReactHtmlParser(
                                  boldSubstring(
                                    plainShortText,
                                    inputSearchValue
                                  )
                                )}
                                {textToDisplay && (
                                  <a
                                    onClick={() => {
                                      changeShowMoreLess(
                                        true,
                                        value.first_name,
                                        value.last_name
                                      );

                                      //showTextArray
                                      //sethowTextArray
                                      //setShowText(true);
                                    }}
                                  >
                                    ...Show More
                                  </a>
                                )}
                              </p>
                            )}
                            {value.showText == true && (
                              <p style={{ justifyContent: "center" }}>
                                {ReactHtmlParser(
                                  boldSubstring(
                                    messageContent,
                                    inputSearchValue
                                  )
                                )}
                                <a
                                  onClick={() => {
                                    changeShowMoreLess(
                                      false,
                                      value.first_name,
                                      value.last_name
                                    );

                                    //sethowTextArray
                                    //setShowText(false)
                                  }}
                                >
                                  {" "}
                                  ...Show Less
                                </a>
                              </p>
                            )}
                          </div>
                        </div>
                      </Col>
                      <Divider className="w-100 mt-2" />
                    </Row>
                  );
                })}
            </div>
         
        </Row>
      )}

      {showTextArray && showTextArray.length < 1 && (
        <div className="mid-content d-flex justify-content-center align-items-center">
          <Row>
            <p> Results for this poll are not available at this moment. </p>
          </Row>
        </div>
      )}
    </React.Fragment>
  );
};

export default ShortAnsLongAnsPollsAns;
