import React from "react";
import { Popover } from "antd";
import DeleteOpenlineModal from "../../components/modal/DeleteOpenlineModal";
import dotsicon from "../../assets/icons/dots-icon.svg";
import { Link } from "react-router-dom";
import { EDIT } from "../../headings";

const content = (test) => (
  <div>
    <ul>
      {test.status_id === 8 ? (
        ""
        
      ) : (
        <Link to={{ pathname: "/orgcc/editopenline", state: test }}>
        <li>
          <a href="/orgcc/editopenline">{EDIT}</a>{" "}
        </li>
      </Link>
      )}
      {test.status_id === 5 ? (
        ""
      ) : (
        <li>
          <DeleteOpenlineModal openlineId={test.openline_id} />
        </li>
      )}
    </ul>
  </div>
);

class OpenlinePopover extends React.Component {
  state = {
    visible: false,
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(this.props.data)}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span className="ico-grid-dots">
            <img src={dotsicon} alt="DotIcon" className="pr-4" />
          </span>
        </Popover>
      </>
    );
  }
}

export default OpenlinePopover;
